.header{
  font-size: 21px;
  padding: 20px;
  font-weight: 700;
  position:static;
  top: 0px;
  background-color: white;
  left: 0;
  right: 0;
  background: none;
  /* background-color: #edc2c2; */
}

header{
  font-family: 'Poppins', sans-serif;
  border: 2px #ebebeb solid;
  border-radius: 50px;
  padding: 10px 15px;
  position: relative;
}
header ul.nav {
  float: right;
  list-style: none;
}
header ul.nav li{
  display: inline;
  margin-left: 50px;
  cursor: pointer;
  border: 2px solid #ca5252;
  border-radius :50px;
  padding: 12px 16px;
}
header .shop-cart-button{
  transition: color, transform 500ms ease;
  transform: scale(1.5);
}
header .shop-cart-button:hover,
header .shop-cart-button.active{
  background-color: #ca5252;
  display: inline-block;
  color: #fff;
  transform: scale(1.2);
}
header .shop-cart{
  position: absolute;
  top:200px;
  right: 0;
  min-height: 100px;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 6px 5px 5px 0px rgba(87,33,87,1);
  -moz-box-shadow: 6px 5px 5px 0px rgba(87,33,87,1);
  box-shadow: 6px 5px 5px 0px rgba(87,33,87,1);
  z-index: 1000;
  padding: 20px;
  padding-bottom: 0px;
  border-radius: 20px;
}
header .shop-cart .empty h2{
  font-size: 20px;
text-align: center;
} 
header .shop-cart .item{
  width: 100%;
  float: left;
  margin-bottom: 20px;
}
header .shop-cart .item img{
  width: 70px;
  float: left;
  margin-right :20px; 
}  
header .shop-cart .item h2{
  font-size: 20px;
  margin-bottom: 10px;
}
header .shop-cart .item p{
  color: #797979;
  font-weight: 600;
}
header .shop-cart .item .delete{
  color: #ca5252;
  float: right;
  position: relative;
  top: -20px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}
header .shop-cart .item .delete:hover{
  color: rgb(152, 32, 32);
  transform: scale(1.4);
}
header .shop-cart .sum {
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
  text-align: center;
  border: 2px solid #ca5252;
  border-radius: 50px;
}
.logo_name{
  display: flex;
  align-items: center;
  justify-content:space-between;

}
.round{
  font-size: 25px;
}
.round {
  transition: 3s ease-in-out;
  background-color: #333;
  border: 3px solid #ca5252;
  color: #fff;
  border-radius :50px;
  padding: 20px;
  cursor: pointer;
 }
/* .round:hover {
  transform: rotate(360deg);
} */
.body_cont{
  width: 100%;
  height: 100vh;
  background-color: #fff;
  color: #222;
}
.footer{
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
}
.wrapper{
  width: 1280px;
  margin: 20px auto;
}

.logo8{
  position: relative;
}


#reviews {
	display: flex;
	flex-flow: row wrap;
}
.presentation{
  display: flex;
  /* justify-content: center;
  align-items: center; */
}
.comment {
  position: relative;
	max-width: 340px; 
	flex-basis: 50%;
  margin-top: 60px;
  margin-left: -0px;
}
.comment_bubble {
  display: flex;
  justify-content: center;
  align-items: center;
	padding: 35px;
	max-width: 470px;
}
.comment_bubble {
	background: #ebebeb;
	position: relative;
	-moz-border-radius: 10px;
	-webkit-border-radius: 10px;
	border-radius: 20px;
	margin-left: 10px;
  width: 310px;
text-align: center;
}
.comment_bubble:before {
	content: "";
    position: absolute;
    right: 100%;
    top: 35px;
    border-top: 12px solid transparent;
    border-right: 13px solid #ebebeb;
    border-bottom: 12px solid transparent;
}
.comment_text{
  font-size: 20px;
}

.logo8{
  width: 260px;

}

.logo_name ul li:hover{
  background-color: #ca5252;
  color: #fff;
}
.categ{
  display: flex;
  justify-content: center;
  align-items: center;
}
.categ div{
  display: inline-block;
  background: #FFD700;
  border-radius: 50px;
  border: 2px solid transparent;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
font-size: 22px;
transition: all 500ms ease;
margin-left: 40px;
}
.categ div:hover{
  border-color:#ca5252 ;
  transform: scale(1.1);
}
.full-item{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.734);
  z-index: 9999;
  overflow: auto;

}
.full-item > div{
width: 450px;
position: relative;
margin: 10% auto;
padding: 40px 30px;
background: #fff;
border-radius: 20px;
}
main{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}
main .item{
  width: 24%;
  margin: 5px;
  margin-bottom: 50px;
  background: #f9f9f9;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;

}
main .item img, .full-item img{
  width: 100%;
  height: 250px;
  border-radius: 20px 20px 0 0 ;
  transition: transform 500ms ease;
  cursor: pointer;
} 
main .item img:hover,.full-item img:hover{
  transform: scale(1.05);
}
footer{
  text-align: center;
  margin-top: 100px;
}
.sizes{
  font-size: 20px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  margin: 10px 20px;
  color: #333;
  right: auto;
  text-align: center;
  list-style: none;
}
main h2, main p , main b,.full-item b,.full-item p,.full-item h2{
  margin: 10px 20px;
  color: #333;

}
main b,.full-item b{
  color: #5fa35f;
}

main .add-to-cart,
.full-item  .add-to-cart{
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 33px;
  position: absolute;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover{
  transform: scale(1.5) translateY(-5px);
  transition: transform 500ms ease;
}
.prices{
font-family: 'Times New Roman', Times, serif;
  font-size: 23px;
  padding: 5px;
}
.categories ul {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}
.categories li::marker{
  marker: none;
}
.categories ul li {
  background-color: #f9f9f9;
  padding: 13px 30px;
  border-radius: 30px;
  margin-right: 10px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.1s ease-in-out;
  margin-bottom: 50px;
  list-style: none;

}
.categories ul li:hover {
  background-color: #f4f4f4;
}
.categories ul li:active {
  background-color: #ececec;
}
.categories ul li.active {
  background-color: #ca5252;
  color: #fff;
}
.sizes{
  display: flex;
  justify-content: center;
  align-items: center;

}
.sizes li{
  background-color: #696969;
  color: #fff;
  font-size: 22px;
  border-radius: 32px;
  margin: 10px;
  padding: 5px;
  width: 30px;
  height: 30px;
  text-align: center;
  cursor: pointer;
}

.sizes li.activeLi{
  background-color: #ca5252;
  color: #fff;
  transform: scale(1.2);
}

